@import url(https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Common Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  outline: none;
  font-family: "Jura", sans-serif;
}

html {
  font-size: 50%;
  scroll-behavior: smooth;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Page BG */
.page-bg {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(/react-portfolio/static/media/bg.150cb94c.png) center no-repeat;
  background-size: cover;
}
/* End of Page BG */

/* Mouse Circle */
.mouse-circle,
.mouse-dot {
  border-radius: 50%;
  position: fixed;
  z-index: 300;
  transform: translate(-50%, -50%);
  opacity: 0;
  pointer-events: none;
}

.mouse-circle {
  width: 6rem;
  height: 6rem;
  border: 0.1rem solid #c7a622;
  animation: mouseCircleAnim 10s infinite linear;
}

@keyframes mouseCircleAnim {
  0% {
    width: 6rem;
    height: 6rem;
  }
  25% {
    width: 8rem;
    height: 8rem;
  }
  35% {
    width: 4rem;
    height: 4rem;
  }
  70% {
    width: 8rem;
    height: 8rem;
  }
  100% {
    width: 6rem;
    height: 6rem;
  }
}

.mouse-dot {
  width: 0.5rem;
  height: 0.5rem;
  background-color: #9b0e0e;
  animation: mouseDotAnim 10s infinite linear;
}

@keyframes mouseDotAnim {
  0% {
    width: 0.5rem;
    height: 0.5rem;
  }
  55% {
    width: 1.5rem;
    height: 1.5rem;
  }
  100% {
    width: 0.5rem;
    height: 0.5rem;
  }
}
/* End of Mouse Circle */
.projects{
    flex-wrap: wrap;
    padding-bottom: 15rem;
}

/* Big Project Image*/
.project-img-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    background-color: rgba(0,0,0,0.9);
    overflow: hidden scroll;
    animation: imgWrapperAnim 1s forwards;
}

@keyframes imgWrapperAnim {
    0%{
        height: 0 ;
    }
    100%{
        height: 100%;
    }
}

.project-img{
    width: 100%;
    border-radius: 0.5rem;
    padding: 7rem 20rem;
    opacity: 0.9;
    transform-origin: top center;
    animation: imgAnim 1s 1s both;
}

@keyframes imgAnim{
    0%{
        transform: scale(0);
    }
    100%{
        transform: scale(1)
    }
}
/* End of  Big Project Image*/
/*  Project Hide Button*/
.project-hide-btn{
    position: fixed;
    top: 5rem;
    right: 5rem;
    background-color: transparent;
    border: none;
    color: #fff;
    font-size: 2rem;
    letter-spacing: 0.1rem;
    cursor:pointer;
    z-index: 10;
    transform: scale(0);
    visibility: hidden;
}

.change.project-hide-btn{
    transform: scale(1);
    visibility: visible;
    transition: transform 0.5s;
}

/* End of Project hide Button*/


.project{
    width: 40rem;
    height: 45rem;
    overflow: hidden;
    margin: 5rem;
    background-color: black;
    display: flex;
    justify-content: center;
    position: relative;
    border: .2rem dotted blue;
    border-radius: .5rem;
    cursor: pointer;
    perspective: 20rem;
}

.project img{
    width: 90%;
    object-fit: cover;
    position: absolute;
    top: 2rem;
    opacity: 0.5;
    transition: top 4s 0.2s, opacity 0.2s;
}

@keyframes oddImageAnim {
    0%{
        transform: rotateY(0);
    }
    25%{
        transform:rotateY(-2deg) rotateX(2deg);
    }
    50%{
        transform:rotateY(2deg) rotateX(-2deg);
    }
    100%{
        transform: rotateY(0);
    }
}
.project:nth-child(odd):hover img{
    animation: oddImageAnim 4s 0.2s;
    opacity: 1;
}

@keyframes evenImageAnim {
    0%{
        transform: rotateY(0);
    }
    25%{
        transform:rotateY(2deg) rotateX(-2deg);
    }
    50%{
        transform:rotateY(-2deg) rotateX(2deg);
    }
    100%{
        transform: rotateY(0);
    }
}
.project:nth-child(even):hover img{
    animation: evenImageAnim 4s 0.2s;
    opacity: 1;
}

/* Section 1 */
.section-1{
    position: relative;
    width: 100%;
    height: 100vh;
}

/* logo */
.logo{
    position: absolute;
    top: 2rem;
    left: 5rem;
    font-size: 2rem;
    text-transform: uppercase;
    color: white;
}
/* End of logo */

/* Animated-circles */
.main-circle{
    width: 55rem;
    height: 55rem;
    position: absolute;
    overflow: hidden;
    border-radius: 50%;
    transition: left 2s, top 2s;
}
    

.main-circle img{
    mix-blend-mode: hard-light; 
    -webkit-filter: brightness(1.18); 
            filter: brightness(1.18);
    position: relative;
    top: 0;
    left: 0;
}

.main-circle img.resize{
    width: 130%;
    height: 130%;
}

.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

.circle{
    width: 22rem;
    height: 22rem;  
    border-radius: 50%;
    background-color: red;
    align-self: center;
    justify-self: center;
    opacity: .5;
    position: relative;
    transition: left 2s, top 2s;
    top: 0;
    left: 0;
}

.circle-1{
    background: url(/react-portfolio/static/media/circle-1-img.7aa62f1e.jpg) center no-repeat;
    background-size: cover;
    background-clip: content-box;
    border: .5rem dotted #231aa1;
    margin-left: -10rem;
}

.circle-2{
    background: url(/react-portfolio/static/media/circle-2-img.3a8fd210.jpg) center no-repeat;
    background-size: cover;
    background-clip: content-box;
    border: .5rem dashed white;
    margin-right: -10rem;
}

.circle-3{
    background: url(/react-portfolio/static/media/circle-3-img.0807b67f.jpg) center no-repeat;
    background-size: cover;
    background-clip: content-box;
    border: 1rem double white;
    margin-left: -10rem;
}

.circle-4{
    background: url(/react-portfolio/static/media/circle-4-img.195613f3.jpg) center no-repeat;
    background-size: cover;
    background-clip: content-box;
    border: .7rem double#231aa1;
    margin-right: -10rem;
}


/* End of animated-circles */

/* Featured-TExt */
.featured-text{
    font-size: 2rem;
    color: white;
    text-transform:uppercase;
    width: 0;
    word-wrap: break-word;
    line-height: 3.4;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.featured-text-1{
    margin: 0 auto 0 10rem;
}
.featured-text-2{
    margin: 0 10rem 0 auto;
}
/* End of Featured-TExt */

.main-btn{
    position: absolute;
    bottom: 3rem;
    width: 13rem;
    height: 13rem;
    background-color:  #231aa1;
    border-radius: 50%;
    border: .2rem dotted     white;
    padding: .2rem;
    overflow: hidden;
}
.main-btn-content{
    width: 100%;
    height: 100%;
    border: .2rem dotted #e7be08;
    border-radius:  50%;
    transform: rotate(40deg);
    transition: transform 0.2s, border 0.2s;
}

.main-btn:hover .main-btn-content{
    transform: rotate(60deg);
    border: 0.2rem dotted #9b0e0e;
    transition: transform 0.2s 0.5s, border 0.2s;
}

.ripple {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #e7be08;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    animation: rippleAnim 0.5s forwards;
}
@keyframes rippleAnim {
    0%{
        width: 0;
        height: 0;
    }
    100%{
        width: 400%;
        height: 400%;
    }
}

.main-btn-line{
    position: absolute;
    top: 4rem;
    width: 3rem;
    height: 1.5rem;
    border: .1rem solid white;
}

.main-btn-line:nth-child(1){
    transform: rotateZ(-45deg) translateX(-0.3rem);
    transform-origin: left center;
   
}

.main-btn-line:nth-child(2){
    transform: rotateZ(45deg) translateX(.3rem);
    transform-origin: right center;
    
}

.main-btn-line:nth-child(3){
    transform: rotateZ(90deg) translateX(.5rem);
   
}



.main-btn span{
    position: absolute;
    bottom: 3rem;
    color: #fff;
    font-size: 1.1rem;
    font-weight: 600;
    text-transform: uppercase;
}
/* End of Section 1 */

.section-3{
    width: 100%;
    height: 100%;
    padding: 20rem 0 10rem 0;
    position: relative;
}
